* {box-sizing: border-box;}
.App {
    text-align: center;
  }
  
  .date {
    font-weight: 800;
  }
  
  .time {
  
  }
  
  .TodoList {
      margin: 0 auto;
      width: 60%;
      border: 1px solid #999;
      
    .inputArea {
      position: relative;
      width: 100%; height: 30px;

      input {
        text-align: center;
        width: 100%;
        height: 100%;
        border: none;
        &:focus-visible {
          outline: none;
          background-color: #fff;
        }
      }
      button {
        position: absolute;
        top: 0; right: 0;
        width: 80px; height: 30px;
        border: none;
      }
    }
    
    .TodoItem {
        display: flex;
        align-items: center;
        padding: 5px 20px;
        background-color: #ecfbff;
        border-bottom: 1px dashed #999;
        box-sizing: border-box;
        
        &:last-child {
          border: none;
        }
        .checkBox {
            width: 10px; height: 10px;
            border: 1px solid #000;
        }
        .title {
            margin-left: 5px;
            text-align: left;
        }
    }
}